/** @jsx jsx */
import React from "react"
import {jsx, Flex, NavLink } from 'theme-ui'
import Logo from '../../content/logo.svg'
import { DarkModeSwitch } from 'react-toggle-dark-mode';
import { useColorMode } from 'theme-ui'

const Head =  () => { 
  const [isDarkMode, setDarkMode] =  useColorMode();

  return (
    <Flex as='nav' sx={{mx:'auto',  position: 'sticky', top: '0px', 
    backgroundColor: 'background', py:3, mb:5,
    alignItems:'baseline',
    flexDirection:['column', 'row'],
    alignContent: 'center',
    alignItems: 'center',
    width: '100%',
    }}>
  <DarkModeSwitch sx={{marginLeft: 2, order:[2,0]}} onChange={() => setDarkMode(isDarkMode === 'default' ? 'dark' : 'default')} 
  checked={isDarkMode === 'default'}
  moonColor={'#ccc'}
  sunColor={'#ccc'}
  tabindex={0}
  role={'button'}
  aria-label={`Switch to ${isDarkMode === 'default' ? 'dark' : 'default'} mode`}
   />
  <NavLink href='/' p={2} sx={{fontSize: 4, fontWeight:'bold', marginLeft:[0,'auto'] , display:'flex'}}>
    <span>Ahmad Alq</span>
    <span sx={{backgroundImage: `url(${Logo})`,
    backgroundRepeat: 'no-repeat',
    backgroundSize:'contain',
    backgroundPosition:'center',
    mx:2,
     width:  8,
     height: 8,
     padding:0,
     display: 'inline-block'
    }}></span>
  </NavLink>

</Flex>

)}
export default Head